.navbar {
  padding: 20px;

  &__box {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  &__box {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  &__logo-link {
    display: block;
  }

  &__logo {
    vertical-align: top;
    width: 200px;
    height: 40px;
  }

  &__menu-tog {
    display: none;
    margin-left: auto;
    width: 37px;
    height: 26px;
    background: url("../images/menu.svg") no-repeat;
    background-size: 37px auto;
    border: none;
  }

  &__tog {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
    z-index: 9999;

    &_open {
      display: block !important;
    }

  }

  &__menu {
    position: relative;
    display: flex;
    flex-direction: row;
    margin: 0 15px;
    padding: 0;
    list-style: none;

    & > li {
      padding: 10px 15px;

      & > a {
        font-family: $font-stack;
        font-size: 18px;
        font-weight: 300;
        color: #000;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }

      }

    }

    & li:hover > ul {
      display: block;
    }

    & ul {
      display: none;
      position: absolute;
      top: 100%;
      background :#fff;
      border-radius: 3px;
      padding: 0;
      margin: 0;
      list-style: none;
      box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);

      & > li {
        margin: 10px 20px;

        & > a {
          font-family: $font-stack;
          font-size: 18px;
          font-weight: 300;
          color: #000;
          text-decoration: none;

          &:hover {
            text-decoration: underline;
          }

        }

      }

    }

    &_ml {
      margin-left: auto;
    }

    &_mr {
      margin-right: auto;
    }

  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

  .navbar {
    padding: 10px 0;

    &__menu-tog {
      display: inline-block;
    }

    &__tog {
      display: none;
      padding-bottom: 60px;
      position: absolute;
      top: 40px;
      left: 0;
      flex-direction: column;
      background: #fff;
      text-align: center;

      & .btn {
        font-size: 24px;
        font-weight: 700;
        color: #0381ea;
        background: #fff;

        &:hover {
          color: #fff;
        }

      }

    }

    &__menu {
      flex-direction: column;
      margin: 0;

      & li {
        margin: 10px 15px;
        text-align: center;

        & a {
          font-size: 24px;

          & span {
            font-weight: 700;
            color: #0381ea;
          }

        }

      }

      & ul {
        display: none;
        position: relative;
        box-shadow: none;

        & > li {
          margin: 10px 15px;
          padding: 10px 15px;

          & > a {
            font-size: 24px;
          }

        }

      }

    }

  }
}

